}

.popup{
  transition: width 200ms ease-out 50ms;
  width:95vw;
  height:50vh;
  background-color:rgb(235,235,235);
  display:grid;
  grid-template-columns: repeat(9,1fr);
  grid-template-rows: fit-content(7vh) fit-content(10vh) fit-content(10vh) fit-content(10vh);

}


