   margin: 8px;
   font-weight: bolder;
   padding-top:8px;
 }

 .search-input{
   grid-row:1/1;
   grid-column:1/10;
   border-radius:8px;  
   padding:8px;
   margin: 8px;
   height: calc( 10vh - ( 8px * 2 ) );
   cursor: pointer;
   font-size: 3.5vh;
   width: calc(100vw - ( 10vh + 16px));
   box-shadow: 1px 1px 1px rgb(200,200,200), -1px -1px 1px rgb(220,220,220),
               1px 1px 1px rgb(20,50,60) inset, -1px -1px 1px rgb(20,50,60) inset;

 }

.close-search{
  width:8vh;
  height:8vh;
  align-self: center;
 }

.search-box{ 
   padding:4px;
   padding-top:8px;
   margin: 8px;
   height: calc( 10vh - ( 8px * 3 ) );
   justify-self:center;
 }

img#nav-bar-icon{
  margin:8px;
 }


.popup{
  transition: height 200ms ease-out 50ms;
  width:100vw;
  height:50vh;
  background-color:rgba(235,235,235,0.8);
  display:grid;
  grid-template-columns: repeat(9,1fr);
  grid-template-rows: fit-content(7vh) fit-content(10vh) fit-content(10vh) fit-content(10vh);
  
}


.popup-closing{
  grid-row:1/1;
  grid-column:10/10;
  margin:8px;
  height:7vh;
  border-bottom: 1px solid rgb(10,50,60);
}
.sign-in-button, .email-us, .join-us{
  color:rgb(240,240,240);
  font-size:3vh;
  font-weight:bold;
  font-family: "Roboto-Medium";
  padding:8px;
  margin:8px;
  grid-row:2/2;
  grid-column:1/8;
  text-align:center;
  background-image: linear-gradient(to top,rgb(70,90,100), rgb(30,50,60));
  border-radius:8px;
}

.email-us{
  grid-row:3/3;
}

.join-us{
  grid-row:4/4 !important;
  grid-column: 1/11 !important;
  background-image:linear-gradient(to top, #0492c2,#0594c4) !important;
  justify-content: stretch;
  border-width:0;

}


footer{
  grid-column:1/13;
  grid-row:5/5;
  background-image:linear-gradient(to left, rgb(30,50,60),rgb(60,80,90),rgb(30,50,60));
