}

.follow{
  border-bottom: 1px solid rgb(240,240,240);
  display:flex;
  margin:8px;
  justify-content: center;
  flex-direction: row;
  font-size:3vh;
  font-weight:bold;
  color:rgb(250,250,250);
  align-items: center;
}
.social-handle{
  display:flex;
  margin:8px;
  flex-direction: row;
  justify-content: space-between;

}

.social-handle img{
  width:15vw;
  height:10vw;
}

.contact{
